import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import ClearRoundedIcon from "@material-ui/icons/ClearRounded";
import ButtonSpinner from "../UI/Spinners/ButtonSpinner";
import { useTranslation } from "react-i18next";
import CheckRoundedIcon from "@material-ui/icons/CheckRounded";

const GeneralDialog = ({
  dialogOpen,
  onCancel,
  onConfirm,
  loading,
  disabled,
  title,
  subtitle,
  contentComponent,
  confirmText,
  confirmIcon,
  backText,
  backIcon,
  fullWidth,
  withoutConfirmBtn,
  disableBackdropClick,
}) => {
  const { t } = useTranslation();
  return (
    <Dialog
      open={dialogOpen}
      onClose={onCancel}
      aria-labelledby="dialog-title"
      fullWidth={fullWidth}
      disableBackdropClick={!!disableBackdropClick}
      disableEscapeKeyDown={!!disableBackdropClick}
    >
      {title && (
        <DialogTitle id="dialog-title" style={{ textAlign: "center" }}>
          {title}
        </DialogTitle>
      )}
      {(subtitle || contentComponent) && (
        <DialogContent>
          {contentComponent}
          <DialogContentText>{subtitle}</DialogContentText>
        </DialogContent>
      )}
      <DialogActions
        style={{
          display: "flex",
          justifyContent: "space-between",
          padding: 15,
        }}
      >
        <Button
          onClick={onCancel}
          variant="contained"
          color="default"
          startIcon={backIcon ? backIcon : <ClearRoundedIcon />}
        >
          {backText ? backText : t("common:cancel")}
        </Button>
        {!withoutConfirmBtn && (
          <Button
            onClick={onConfirm}
            variant="contained"
            color="primary"
            startIcon={confirmIcon ? confirmIcon : <CheckRoundedIcon />}
            endIcon={loading ? <ButtonSpinner /> : null}
            disabled={loading || disabled}
          >
            {confirmText}
          </Button>
        )}
      </DialogActions>
    </Dialog>
  );
};

export default GeneralDialog;
