import React, {
  useRef,
  useState,
  useEffect,
  Fragment,
  useCallback,
} from "react";
import Ttext from "../../components/common/Ttext";
import { Link } from "react-router-dom";
import Globals from "../../util/globals";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import * as actions from "../../store/actions";
import {
  Box,
  Typography,
  Grid,
  Hidden,
  Tooltip,
  IconButton,
  Paper,
} from "@material-ui/core";
import Input from "../../components/Inputs/Input";
import SquareButton from "../../components/Buttons/SquareButton";
import { tableIcons } from "../../util/tableIcons";
import MaterialTable from "material-table";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import DeleteRoundedIcon from "@material-ui/icons/DeleteRounded";
import i18n from "../../i18n";
import IconTextNotice from "../../components/common/IconTextNotice";
import CategoryRoundedIcon from "@material-ui/icons/CategoryRounded";
import IosSwitch from "../../components/Inputs/IosSwitch";
import { arSA } from "date-fns/locale";
import TableSkeleton from "../../components/common/TableSkeleton";
import GeneralDialog from "../../components/common/GeneralDialog";
import PageError from "../../components/common/PageError";

const tableHeaders = [
  {
    title: <Ttext ns="common" text="image" />,
    field: "image",
    width: 1,
    render: (row) => {
      return (
        <div className="border-light p">
          <img
            alt={row.title}
            src={`${Globals.server}/${row.image}`}
            width={50}
          />
        </div>
      );
    },
  },
  {
    title: <Ttext ns="common" text="name" />,
    field: "title",
  },
];

const initialQuery = {
  q: "",
  page: 0,
  perPage: 10,
  sort: "date_added",
  direction: "asc",
};

let timeout;

const Categories = ({ location }) => {
  const [query, setQuery] = useState(initialQuery);
  const [deleteDialog, setDeleteDialog] = useState({
    open: false,
    selected: null,
  });

  const categories = useSelector((state) => state.categories);
  const [mounted, setMounted] = useState(false);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const tableRef = useRef();
  const hasPagination = categories.categories.pagination;
  const handleQuery = (value, validity, name) => {
    setQuery((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const fetchCategories = useCallback(async () => {
    try {
      await dispatch(actions.getCategories(query));
    } finally {
      setMounted(true);
    }
  }, [dispatch, query]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchCategories();
    }, 500);
    return () => clearTimeout(timeout);
  }, [query, fetchCategories]);

  const handleSwitchState = (data) => {
    const id = data.category_id;
    const status = data.status > 0 ? 0 : 1;
    dispatch(actions.switchCategoryStatus(id, status));
  };

  const handleTriggerDeleteDialog = (selected) => {
    setDeleteDialog({
      open: !deleteDialog.open,
      selected: selected,
    });
  };

  const handleDeleteCategory = async () => {
    const id = deleteDialog.selected.category_id;
    await dispatch(actions.deleteCategory(id));
    handleTriggerDeleteDialog();
  };

  return (
    <Fragment>
      <Box>
        <Typography variant="h4" className="py">
          {t("categories:categories_list")}
        </Typography>
        <Grid container spacing={2} alignItems="center" className="py">
          <Grid item sm={8} xs={12}>
            <Input
              label={t("common:searchhhh")}
              getResult={handleQuery}
              name="q"
              initialValue={query.q}
            />
          </Grid>
          <Hidden smDown>
            <Grid item md={2} />
          </Hidden>
          <Grid item md={2} sm={4} xs={12}>
            <Link to={`${location.pathname}/add`}>
              <SquareButton
                title={t("categories:new_category")}
                wIcon={
                  <CategoryRoundedIcon
                    className="text-light"
                    fontSize="large"
                  />
                }
                py={0.6}
                px={0.8}
                type="primary"
              />
            </Link>
          </Grid>
        </Grid>
      </Box>
      {!mounted ? (
        <Paper>
          <TableSkeleton row={2} />
        </Paper>
      ) : !categories.loading && categories.errors ? (
        <PageError
          text={categories.errors.message || t("common:uncommon_err")}
          onTryAgain={fetchCategories}
        />
      ) : (
        <MaterialTable
          icons={tableIcons}
          columns={tableHeaders}
          tableRef={tableRef}
          data={categories.categories.categories}
          components={{
            Toolbar: (props) => " ",
            Actions: (props) => {
              const cat = props.data;
              return (
                <>
                  <Tooltip title={t("common:edit")}>
                    <Link to={`${location.pathname}/edit/${cat.category_id}`}>
                      <IconButton>
                        <EditRoundedIcon color="secondary" />
                      </IconButton>
                    </Link>
                  </Tooltip>
                  <Tooltip title={t("common:delete")}>
                    <IconButton onClick={() => handleTriggerDeleteDialog(cat)}>
                      <DeleteRoundedIcon color="error" />
                    </IconButton>
                  </Tooltip>
                  <IosSwitch
                    state={cat.status}
                    onChange={() => handleSwitchState(cat)}
                    name="status"
                    loading={categories.itemLoading}
                  />
                </>
              );
            },
          }}
          actions={[
            {
              icon: () => null,
              onClick: () => null,
              hidden: true,
            },
          ]}
          options={{
            detailPanelColumnAlignment: "right",
            actionsColumnIndex: -1,
            draggable: false,
            emptyRowsWhenPaging: false,
            search: false,
            pageSize: query.perPage,
            pageSizeOptions: [5, 10, 20, 50],
            thirdSortClick: false,
            showTitle: false,
          }}
          onChangeRowsPerPage={(size) => handleQuery(size, true, "perPage")}
          onOrderChange={(orderBy, direction) => {
            const sort = tableHeaders[orderBy].field;
            setQuery((prev) => {
              return {
                ...prev,
                sort: sort,
                direction: direction,
              };
            });
          }}
          totalCount={hasPagination ? hasPagination.totalCount : 0}
          page={hasPagination ? hasPagination.currentPage : 1}
          onChangePage={(page) => handleQuery(page, true, "page")}
          localization={{
            header: {
              actions: "",
            },
            body: {
              emptyDataSourceMessage: categories.loading
                ? t("common:loadingggg")
                : !categories.loading &&
                  !categories.errors && (
                    <IconTextNotice
                      Icon={CategoryRoundedIcon}
                      text={t("categories:no_categories")}
                    />
                  ),
              dateTimePickerLocalization: i18n.language === "ar" ? arSA : "",
            },
            pagination: {
              labelDisplayedRows:
                "{from}-{to} " + t("common:from") + " {count}",
              labelRowsSelect: t("common:perpage"),
            },
          }}
          isLoading={categories.loading}
        />
      )}
      <GeneralDialog
        dialogOpen={deleteDialog.open}
        confirmText={t("common:confirm")}
        title={t("common:confirm_delete")}
        subtitle={t("categories:confirm_delete_info")}
        onCancel={() => handleTriggerDeleteDialog()}
        onConfirm={handleDeleteCategory}
        loading={categories.itemLoading}
        disableBackdropClick
      />
    </Fragment>
  );
};

export default Categories;
